@use 'mixin' as m;
html {
  margin-top: 0 !important;
  --sky: #8ed1f1;
  --color: #4378a3;
  //--color2: #4b87b6;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}
body {
  height: 100vh;
  background: var(--sky);
  &::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/weather-icos.gif);
    background-size: 900px;
    opacity: 0.3;
    animation: bg 5000s linear infinite;
    @keyframes bg {
      0%{
        background-position: 0 0;
      }
      100%{
        background-position: -50000px -50000px;
      }
    }
  }//body before
}//body

h1{
  z-index: 1;
  position: absolute;
  perspective: 700px;
  width: min(40%, 200px);
  left: 50%;
  top: 0;
  transform: translate(-50%, -60%);
  img{
    width: 100%;
    opacity: 0.7;
    animation: rotate 10s infinite;
  }
  @keyframes rotate {
    0%{transform: rotateX(0deg)}
    50%{transform: rotateX(360deg)}
    100%{transform: rotateX(360deg)}
  }
}//h1

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  padding: 5px;
  box-sizing: border-box;
  background: rgba(#FFF, .7);
  border-radius: 10px;
  border: 1px solid rgba(#FFF, .5);
  animation: grow 2s infinite alternate;
  @media screen and (min-width:900px) {
    min-height: 760px;
  }
  @keyframes grow {
    0%{box-shadow: 0 0 10px 5px rgba(#FFF, .3);}
    100%{box-shadow: 0 0 15px 10px rgba(#FFF, .5);}
  }
  .app-inner{
    position: relative;
    height: 100%;
    @media screen and (min-width:900px) {
      display: flex;
      flex-wrap: wrap;
      overflow: hidden;
    }//app-inner min
    @media screen and (max-width:900px) {
      overflow: hidden;
      overflow-y: auto;
      overflow-x: hidden;
    }//app-inner max
    section{
      box-sizing: border-box;
      @media screen and (min-width:900px) {
        width: 50%;
        height: 50%;
      }//section min
      @media screen and (max-width:900px) {
        margin: 20px 0;
      }//section-inner max
      .section-inner{
        position: relative;
        padding: 10px;
        box-sizing: border-box;
        border-radius: 10px;
        min-height: 250px; //새로고침 시 preloader 뭉개지는 것 방지
        @media screen and (min-width:900px) { //화면이 클 때 스크롤바 달아주고 오버플로우
          height: 100%;
          overflow: auto;
        }//section-inner min
        .preloader{
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          background: var(--sky);
          border-radius: 10px;
        }//preloader
        h2{
          font-weight: 600;
          text-transform: uppercase;
          padding: 5px 10px;
          background: var(--color);
          color: #FFF;
          border-radius: 5px;
          @media screen and (min-width:900px) {
            position: sticky;
            top: 0px; //부모가 오버플로우일 땐 부모기준 상대좌표로 0, 원래는 브라우저 기준인데 부모가 오버플로어가 적용돼서 스크롤바가 생기고 부모기준으로 바뀜
            left: 0;
            z-index: 1;
          }//900이상
        }//h2
      }//section-inner
    }//section
  }//app-inner
}//root

body, .app-innerr, .section-inner{
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color);
    }
  } //media hover
}//app-inner

/* spinner ---------------------------------------------------------------------------------------- */
