@use 'mixin' as m;

.comp-hourly{
  .section-inner{
    h2{
      position: sticky;
      left: 0;
    }
    padding-top: 0 !important;
    overflow: auto;
    @media screen and (min-width:900px) {
      display: flex;
      flex-direction: column;
    }//section-inner 900이상
    .wrap{
      position: relative;
      display: inline-table; //변칙방법 자동으로 자식의 모양에 따라 변하는...
      @media screen and (min-width:900px) {
        height: calc(100% - 25px);
      }//wrap 900이상
      @media screen and (max-width:900px) {
        height: 400px;
      }//wrap 900이하
      ul{
        display: inline-flex; //li들이 계속 커지면 ul도 같이 늘어나게
        li{
          box-sizing: border-box;
          padding-top: 20px;
          width: 70px;
          height: 100%;
          &:nth-child(even){
            background: rgba(#FFF, 0.5);
          }
          p{
            margin: 3px 0;
            font-size: 0.9em;
            text-align: center;
            img{
              display: inline-block;
              width: 80%;
            }//img
          }//p
        }//li
      }//ul
      .chart-wrap{
        position: absolute; //ul하고 겹치게
        left: 5px;
        bottom: 0;
        width: calc(100% - 35px);
        height: 50%;
      }
    }//wrap
  }//section-inner
}//comp-hourly