@use 'mixin' as m;
section.comp-current{
  @media screen and (max-width:900px) {
    margin-top: 30px !important;
  }//section 900이하 모바일사이즈
  .section-inner{
    .current-output{
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      border-radius: 10px;
      background-position: center; //background가 적용돼있음 얘랑 사이즈 적용 안 됨^^;
      background-size: cover;
      color: #FFF;
      @media screen and (min-width:900px) {
        height: 100%;
      }//current-output 900이상 , 라지 사이즈
      @media screen and (max-width:900px) {
        padding: 10px;
      }//current-output 900이하
      button.reload{
        z-index: 1;
        position: absolute;
        width: 50px;
        height: 50px;
        right: 5%;
        top: 5%;
        font-size: 1.3em;
        opacity: .7;
        transition: all 0.5s;
        &:active, &:hover{
          opacity: 1;
          transform: rotate(360deg);
        }
      }
      address{
        display: flex; // 부모까지 white-space: nowrap;을 하면 애니메이션 할 때 삐끗하는 부분이 생김
        overflow: hidden;
        margin-bottom: 10px;
        width: min(90%, 350px);
        box-sizing: border-box;
        padding: 3px 10px;
        background: rgba(#FFF, 0.2);
        border-radius: 5px;
        span{
          display: inline-block;
          white-space: nowrap;
          font-weight: 100;
          animation: marquee 20s infinite linear;
        }//span
        @keyframes marquee {
          0%{transform: translateX(0%);}
          100%{transform: translateX(-100%);}
        }//marquee animation
         // 0%{transform: translate: ;} 100%
      }//address
      .circle-container{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: min(85%, 300px);
        border-radius: 50%;
        background: rgba(#000, 0.2);
        @include m.square;
        div.sunset{
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg{
            width: 100%;
            .sun{
              fill: rgb(248, 193, 111);
              offset-path: path('M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175');
              //offset-distance: 100%; 인라인 스타일로 바인딩하기 -> HTML 요소 내부에 style 속성을 사용하여 CSS 스타일을 적용하는 방법
              animation: rotate-sun 100s;
              @keyframes rotate-sun {
                0%{transform: rotate(0deg) scale(0.6);}
                100%{transform: rotate(360deg) scale(0.6);}
              }
            }//sun path
            .line{
              fill: none;
              stroke: rgba(#FFF, 0.5);
              stroke-width: 2px;
            }//line
            .dash{
              fill: none;
              stroke: rgb(255, 196, 0);
              stroke-width: 4px;
              stroke-dasharray: 1;
              //stroke-dashoffset: 1; //1~0으로 바껴야 함 1은 해가 안 뜸, 0은 해가 지는 시간 .5는 해가 중앙
            }//dash
          }//svg
          time{
            display: inline-block;
            font-size: 0.85em;
            padding: 3px 5px;
            &.rise-time{
              transform: translateX(-30%);
            }
            &.set-time{
              float: right;
              padding: 3px 5px;
              transform: translateX(30%);
            }
          }//time
        }//sunset
        .circle-container-inner{
          @include m.posLT(absolute, 50%, 50%, -50%, -50%);
          width: 90%;
          .temp{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            span{
              position: absolute;
              transform: translate(50px, 10px);
              i{
                margin-right: 5px;
                font-size: 1.1em;
              }
            }//span
            em{
              font-size: 2em;
              margin-right: -50px;
              position: relative;
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup{
                font-size: 0.7em;
              }//sup 윗첨자
            }//em
            img{
              width: 100px;
              mask-image: url(../assets/img/mask.png);
              mask-size: 100%;
              mask-position: center;
              mask-repeat: no-repeat;
            }//img
          }//temp
          .desc{
            margin-top: -15px;
            text-align: center;
            font-size: 1.2em;
          }
          .etc{
            width: 70%;
            margin: 3px auto;
            text-align: center;
            padding: 5px;
            border-radius: 5px;
            box-sizing: border-box;
            background: rgba(#000, 0.3);
            span{
              display: inline-block;
              margin: 1px 5px;
              font-size: 0.9em;
                i{
                  margin-right: 3px;
                  font-size: 0.8em;
                  &.fa-location-arrow{
                    margin-right: 10px;
                  }
                }//i
                small{
                  font-size: 0.8em;
                }//small
            }//span
          }//etc
            .time-date{
              margin-top: 5px;
              text-align: center;
              color: #FFF;
              time{
                display: block;
                &.time{
                  font-size: 1.3em;
                  small{
                    font-size: 0.7em;
                  }//small
                }
              }//time
            }//time-date
        }//circle-container-inner
      }//circle-container
    }//current-output
  }//section-inner
}//section.comp-current