html {
  margin-top: 0 !important;
  --sky: #8ed1f1;
  --color: #4378a3;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background: var(--sky);
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/weather-icos.gif);
  background-size: 900px;
  opacity: 0.3;
  animation: bg 5000s linear infinite;
}
@keyframes bg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -50000px -50000px;
  }
}

h1 {
  z-index: 1;
  position: absolute;
  perspective: 700px;
  width: min(40%, 200px);
  left: 50%;
  top: 0;
  transform: translate(-50%, -60%);
}
h1 img {
  width: 100%;
  opacity: 0.7;
  animation: rotate 10s infinite;
}
@keyframes rotate {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(360deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

#root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  padding: 5px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  animation: grow 2s infinite alternate;
}
@media screen and (min-width: 900px) {
  #root {
    min-height: 760px;
  }
}
@keyframes grow {
  0% {
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 15px 10px rgba(255, 255, 255, 0.5);
  }
}
#root .app-inner {
  position: relative;
  height: 100%;
}
@media screen and (min-width: 900px) {
  #root .app-inner {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner {
    overflow: hidden;
    overflow-y: auto;
    overflow-x: hidden;
  }
}
#root .app-inner section {
  box-sizing: border-box;
}
@media screen and (min-width: 900px) {
  #root .app-inner section {
    width: 50%;
    height: 50%;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner section {
    margin: 20px 0;
  }
}
#root .app-inner section .section-inner {
  position: relative;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 10px;
  min-height: 250px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner {
    height: 100%;
    overflow: auto;
  }
}
#root .app-inner section .section-inner .preloader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--sky);
  border-radius: 10px;
}
#root .app-inner section .section-inner h2 {
  font-weight: 600;
  text-transform: uppercase;
  padding: 5px 10px;
  background: var(--color);
  color: #FFF;
  border-radius: 5px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner h2 {
    position: sticky;
    top: 0px;
    left: 0;
    z-index: 1;
  }
}

@media (hover: hover) {
  body::-webkit-scrollbar-track, .app-innerr::-webkit-scrollbar-track, .section-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  body::-webkit-scrollbar, .app-innerr::-webkit-scrollbar, .section-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  body::-webkit-scrollbar-thumb, .app-innerr::-webkit-scrollbar-thumb, .section-inner::-webkit-scrollbar-thumb {
    background-color: var(--color);
  }
}

