@use 'mixin' as m;
.comp-daily{
  .section-inner{
    padding-top: 0 !important; //부모에 들어가 있는 패딩에서 top만 0으로 만들어줌
    ul{
      li{
        padding: 5px;
        border: 1px solid rgba(skyblue, 0.5);
        a{
          position: relative;
          display: block;
          padding: 10px;
          transition: all 0.5s;
          &.active{
            background: var(--blue);
            color: #FFF;
            .fa-chevron-down{
              transform: translateY(-50%) rotate(180deg);
            }//fa-chevron-down active
          }
          .fa-chevron-down{
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
          }//화살표
          .date{
            margin-bottom: 10px;
            font-weight: 500;
            span{
              margin-right: 5px;
              &.desc{
                font-style: italic;
              }//span span
            }//span
          }//date
          .info{
            img{
              display: inline-block;
              vertical-align: middle;
              width: 50px;
              margin-right: 10px;
            }//img
            span{
              margin-right: 10px;
              i{
                margin-right: 10px;
              }//i
              em{
                font-weight: 600;
              }//em
            }//span
          }//info
          &.active{
            background: var(--dark);
            color: #FFF;
          }//a active
        }//a(button)
        .detail{
          display: none;
          padding: 10px;
          color: #FFF;
          background: rgba(rgb(107, 164, 187), 0.5);
          p{
            padding: 7px 0;
            border-bottom: 1px solid #FFF;
            &:last-child{
              border: none;
            }
            span{
              margin-right: 10px;
              b{
                margin: 0 5px;
              }//b
              em{
                font-weight: 600;
                small{
                  font-size: 0.85em;
                }//small
              }//em
            }//span
          }//p
        }//detail
      }//li
    }//ul
  }//section-inne
}//comp-daily