.comp-daily .section-inner {
  padding-top: 0 !important;
}
.comp-daily .section-inner ul li {
  padding: 5px;
  border: 1px solid rgba(135, 206, 235, 0.5);
}
.comp-daily .section-inner ul li a {
  position: relative;
  display: block;
  padding: 10px;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a.active {
  background: var(--blue);
  color: #FFF;
}
.comp-daily .section-inner ul li a.active .fa-chevron-down {
  transform: translateY(-50%) rotate(180deg);
}
.comp-daily .section-inner ul li a .fa-chevron-down {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.comp-daily .section-inner ul li a .date {
  margin-bottom: 10px;
  font-weight: 500;
}
.comp-daily .section-inner ul li a .date span {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .date span.desc {
  font-style: italic;
}
.comp-daily .section-inner ul li a .info img {
  display: inline-block;
  vertical-align: middle;
  width: 50px;
  margin-right: 10px;
}
.comp-daily .section-inner ul li a .info span {
  margin-right: 10px;
}
.comp-daily .section-inner ul li a .info span i {
  margin-right: 10px;
}
.comp-daily .section-inner ul li a .info span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li a.active {
  background: var(--dark);
  color: #FFF;
}
.comp-daily .section-inner ul li .detail {
  display: none;
  padding: 10px;
  color: #FFF;
  background: rgba(107, 164, 187, 0.5);
}
.comp-daily .section-inner ul li .detail p {
  padding: 7px 0;
  border-bottom: 1px solid #FFF;
}
.comp-daily .section-inner ul li .detail p:last-child {
  border: none;
}
.comp-daily .section-inner ul li .detail p span {
  margin-right: 10px;
}
.comp-daily .section-inner ul li .detail p span b {
  margin: 0 5px;
}
.comp-daily .section-inner ul li .detail p span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail p span em small {
  font-size: 0.85em;
}